:root {
  --text-color: #212121;
  --white-text-color: #fff;

  --profile-stats-bg-color: rgb(245, 244, 244);
  --profile-stats-border-color: #ccc;

  --online-status-color: rgb(12, 177, 12);
  --offline-status-color: rgb(248, 20, 20);

  --thead-bg-color: rgb(44, 164, 211);
  --trow-bg-color: #efefef;
  --trow-accent-bg-color: rgb(97, 122, 177);
  --table-border-color: #f9f9f9;

  --card-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12),
    0px 4px 4px rgba(0, 0, 0, 0.06), 1px 4px 6px rgba(0, 0, 0, 0.16);
  --animation: 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

body {
  margin: 0;
  padding-top: 20px;
  padding-bottom: 20px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

h2 {
  margin-top: 0;
  text-transform: uppercase;
}
